"use client";

import { useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { MemoryEvent } from "../api";

interface MemoryStreamProps {
    events: MemoryEvent[];
}

export default function MemoryStream({ events }: MemoryStreamProps) {
    const scrollRef = useRef<HTMLDivElement>(null);

    // Auto-scroll to bottom
    useEffect(() => {
        if (scrollRef.current) {
            scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
        }
    }, [events]);

    return (
        <div className="w-full h-64 bg-black/50 border border-gray-800 rounded-lg p-4 font-mono text-xs overflow-hidden flex flex-col">
            <div className="text-gray-500 mb-2 border-b border-gray-800 pb-1 flex justify-between">
                <span>MEMORY STREAM</span>
                <span className="animate-pulse text-green-500">● LIVE</span>
            </div>

            <div ref={scrollRef} className="flex-1 overflow-y-auto space-y-1 scrollbar-hide">
                <AnimatePresence>
                    {events.map((event, i) => (
                        <motion.div
                            key={`${event.timestamp}-${i}`}
                            initial={{ opacity: 0, x: -20 }}
                            animate={{ opacity: 1, x: 0 }}
                            className="flex gap-2 text-gray-300 hover:bg-white/5 p-1 rounded transition-colors"
                        >
                            <span className="text-gray-600 shrink-0">
                                {new Date(event.timestamp).toLocaleTimeString()}
                            </span>
                            <span className={`shrink-0 font-bold ${getTypeColor(event.type || 'unknown')}`}>
                                [{(event.type || 'unknown').toUpperCase()}]
                            </span>
                            <span className="truncate">{event.content || ''}</span>
                        </motion.div>
                    ))}
                </AnimatePresence>
                {events.length === 0 && (
                    <div className="text-gray-700 text-center mt-10">Waiting for resonance...</div>
                )}
            </div>
        </div>
    );
}

function getTypeColor(type: string) {
    switch (type) {
        case "goal": return "text-blue-400";
        case "feedback": return "text-purple-400";
        case "thought": return "text-yellow-400";
        case "error": return "text-red-400";
        default: return "text-gray-400";
    }
}
