"use client";

import { motion } from "framer-motion";
import { DreamAnalysis } from "../api";
import { Brain, AlertTriangle, CheckCircle } from "lucide-react";

interface DreamGalleryProps {
    analysis: DreamAnalysis | null;
}

export default function DreamGallery({ analysis }: DreamGalleryProps) {
    if (!analysis) {
        return (
            <div className="w-full h-48 bg-zinc-900/50 rounded-2xl p-8 border border-zinc-800 flex items-center justify-center text-zinc-600">
                Waiting for first dream cycle...
            </div>
        );
    }

    const { black_hole, white_hole, conclusion } = analysis;

    return (
        <div className="w-full bg-zinc-900/50 rounded-2xl p-8 border border-zinc-800">
            <div className="flex justify-between items-center mb-6">
                <h2 className="text-zinc-500 text-xs tracking-widest flex items-center gap-2">
                    <Brain size={14} />
                    SUBCONSCIOUS ANALYSIS
                </h2>
                <div className="text-[10px] text-zinc-600 font-mono">
                    {new Date(analysis.timestamp).toLocaleString()}
                </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">

                {/* Black Hole Stats */}
                <div className="space-y-2">
                    <div className="text-xs text-zinc-400 font-bold">BLACK HOLE (Input)</div>
                    <StatRow label="Entropy" value={black_hole.singularity_entropy.toFixed(4)} />
                    <StatRow label="Horizon" value={black_hole.event_horizon_radius.toFixed(2)} />
                    <StatRow label="Radiation" value={black_hole.hawking_radiation.intensity.toFixed(2)} />
                </div>

                {/* White Hole Stats */}
                <div className="space-y-2">
                    <div className="text-xs text-zinc-400 font-bold">WHITE HOLE (Output)</div>
                    <StatRow label="Fidelity" value={(white_hole.reconstruction_fidelity * 100).toFixed(1) + "%"} />
                    <StatRow label="Info Loss" value={(white_hole.information_loss * 100).toFixed(1) + "%"} />
                </div>

                {/* Conclusion */}
                <div className={`p-4 rounded-lg border ${conclusion.system_healthy ? "bg-green-500/10 border-green-500/30" : "bg-red-500/10 border-red-500/30"}`}>
                    <div className="flex items-center gap-2 mb-2">
                        {conclusion.system_healthy ? (
                            <CheckCircle size={16} className="text-green-500" />
                        ) : (
                            <AlertTriangle size={16} className="text-red-500" />
                        )}
                        <span className={`text-sm font-bold ${conclusion.system_healthy ? "text-green-400" : "text-red-400"}`}>
                            {conclusion.system_healthy ? "HEALTHY" : "RISK DETECTED"}
                        </span>
                    </div>
                    <p className="text-xs text-zinc-300 leading-relaxed">
                        {conclusion.message}
                    </p>

                    {/* Risk Tags */}
                    <div className="flex gap-2 mt-3">
                        {conclusion.obsession_risk && <RiskTag label="OBSESSION" />}
                        {conclusion.bias_risk && <RiskTag label="BIAS" />}
                        {conclusion.fear_risk && <RiskTag label="FEAR" />}
                    </div>
                </div>

            </div>
        </div>
    );
}

function StatRow({ label, value }: { label: string, value: string }) {
    return (
        <div className="flex justify-between text-xs font-mono border-b border-zinc-800 pb-1">
            <span className="text-zinc-500">{label}</span>
            <span className="text-zinc-300">{value}</span>
        </div>
    );
}

function RiskTag({ label }: { label: string }) {
    return (
        <span className="px-2 py-0.5 bg-red-500/20 text-red-400 text-[10px] rounded border border-red-500/30">
            {label}
        </span>
    );
}
