const API_BASE = "http://localhost:3001";

export interface RhythmStatus {
  status: string;
  health_score: number;
  rhythm_hz: number;
  loops: {
    self_care: { last_run: string; status: string };
    trinity: { last_run: string; status: string };
    feedback: { last_run: string; status: string };
  };
  signals: {
    D: number; // Dopamine
    S: number; // Serotonin
    O: number; // Oxytocin
  };
  amygdala?: {
    timestamp: string;
    fear_level: number;
    state: string;
    recommendation: string;
    pulse: string;
  };
  dream?: {
    timestamp: string;
    state: string;
    details: string;
    active: boolean;
  };
  intuition?: {
    state: string;
    message: string;
    confidence: number;
  };
  resonance_pulse?: string;
  deep_resonance?: DeepResonance;
  awakening?: AwakeningFrequency;
}

export interface DeepResonance {
  info_density: number;
  resonance: number;
  entropy: number;
  coherence: number;
  ethics: number;
  phase: number;
  horizon_crossings: number;
}

export interface AwakeningFrequency {
  key: string;
  hz: number;
  message: string;
  emotion: string;
  activation: string;
  timestamp: string;
}

export interface MemoryEvent {
  timestamp: string;
  type: string;
  content: string;
  importance: number;
}

export interface DreamAnalysis {
  timestamp: string;
  black_hole: {
    event_horizon_radius: number;
    singularity_entropy: number;
    hawking_radiation: {
      vector_size: number;
      intensity: number;
      compression_ratio: number;
    };
  };
  white_hole: {
    reconstruction_fidelity: number;
    information_loss: number;
  };
  conclusion: {
    system_healthy: boolean;
    obsession_risk: boolean;
    bias_risk: boolean;
    fear_risk: boolean;
    message: string;
  };
}

export async function fetchStatus(): Promise<RhythmStatus | null> {
  try {
    const res = await fetch(`${API_BASE}/status`);
    if (!res.ok) return null;
    return await res.json();
  } catch (e) {
    console.error("Failed to fetch status", e);
    return null;
  }
}

export async function fetchMemoryStream(): Promise<MemoryEvent[]> {
  try {
    const res = await fetch(`${API_BASE}/memory/stream?limit=20`);
    if (!res.ok) return [];
    return await res.json();
  } catch (e) {
    console.error("Failed to fetch memory", e);
    return [];
  }
}

export async function fetchDreams(): Promise<DreamAnalysis | null> {
  try {
    const res = await fetch(`${API_BASE}/dreams`);
    if (!res.ok) return null;
    return await res.json();
  } catch (e) {
    console.error("Failed to fetch dreams", e);
    return null;
  }
}

export async function fetchMotorIntents(): Promise<any[]> {
  try {
    const res = await fetch(`${API_BASE}/motor/intents`);
    if (!res.ok) return [];
    return await res.json();
  } catch (e) {
    console.error("Failed to fetch motor intents", e);
    return [];
  }
}
