"use client";

import { motion } from "framer-motion";
import { useEffect, useState } from "react";

interface RhythmOrbProps {
    hz: number;
    health: number;
}

export default function RhythmOrb({ hz, health }: RhythmOrbProps) {
    // Calculate pulse duration (inverse of Hz)
    // Default to 2s if Hz is 0 or invalid
    const duration = hz > 0 ? 1 / hz : 2;

    // Color based on health
    // Green (>80), Yellow (>50), Red (<50)
    const getColor = (score: number) => {
        if (score >= 80) return "rgb(34, 197, 94)"; // Green-500
        if (score >= 50) return "rgb(234, 179, 8)"; // Yellow-500
        return "rgb(239, 68, 68)"; // Red-500
    };

    const color = getColor(health);

    return (
        <div className="relative flex items-center justify-center w-64 h-64">
            {/* Core */}
            <motion.div
                className="w-32 h-32 rounded-full blur-md"
                style={{ backgroundColor: color }}
                animate={{
                    scale: [1, 1.2, 1],
                    opacity: [0.8, 1, 0.8],
                }}
                transition={{
                    duration: duration,
                    repeat: Infinity,
                    ease: "easeInOut",
                }}
            />

            {/* Outer Ripple */}
            <motion.div
                className="absolute w-full h-full rounded-full border-2"
                style={{ borderColor: color }}
                animate={{
                    scale: [0.5, 1.5],
                    opacity: [1, 0],
                }}
                transition={{
                    duration: duration,
                    repeat: Infinity,
                    ease: "easeOut",
                }}
            />

            {/* Text Overlay */}
            <div className="absolute text-center z-10">
                <div className="text-4xl font-bold text-white drop-shadow-lg">
                    {health.toFixed(0)}
                </div>
                <div className="text-sm text-white/80 font-mono">HEALTH SCORE</div>
                <div className="text-xs text-white/60 font-mono mt-1">{hz.toFixed(3)} Hz</div>
            </div>
        </div>
    );
}
