"use client";

import { useEffect, useRef, useState } from "react";
import { Volume2, VolumeX } from "lucide-react";

interface AuditoryCortexProps {
    hz: number;
    health: number;
}

export default function AuditoryCortex({ hz, health }: AuditoryCortexProps) {
    const [isPlaying, setIsPlaying] = useState(false);
    const [currentTrack, setCurrentTrack] = useState<string | null>(null);
    const audioRef = useRef<HTMLAudioElement | null>(null);
    const [playlist, setPlaylist] = useState<string[]>([]);

    // Fetch playlist on mount
    useEffect(() => {
        fetch("http://localhost:3001/music/list")
            .then(res => res.json())
            .then(data => {
                if (Array.isArray(data) && data.length > 0) {
                    setPlaylist(data);
                }
            })
            .catch(err => console.error("Failed to fetch playlist", err));
    }, []);

    const playRandomTrack = () => {
        if (playlist.length === 0) return;
        const randomTrack = playlist[Math.floor(Math.random() * playlist.length)];
        const url = `http://localhost:3001/music/${randomTrack}`;
        setCurrentTrack(randomTrack);

        if (audioRef.current) {
            audioRef.current.src = url;
            audioRef.current.play().catch(e => console.error("Play failed", e));
        }
    };

    const toggleAudio = () => {
        if (isPlaying) {
            if (audioRef.current) {
                audioRef.current.pause();
            }
            setIsPlaying(false);
        } else {
            setIsPlaying(true);
            if (!currentTrack) {
                playRandomTrack();
            } else if (audioRef.current) {
                audioRef.current.play().catch(e => console.error("Play failed", e));
            }
        }
    };

    // Auto-play next track
    const handleEnded = () => {
        playRandomTrack();
    };

    return (
        <div className="flex items-center gap-2">
            <audio
                ref={audioRef}
                onEnded={handleEnded}
                onError={(e) => console.error("Audio error", e)}
            />
            <button
                onClick={toggleAudio}
                className={`p-2 rounded-full transition-colors ${isPlaying ? "bg-green-500/20 text-green-500" : "bg-zinc-800 text-zinc-500 hover:text-zinc-300"
                    }`}
                title={isPlaying ? `Playing: ${currentTrack}` : "Enable Auditory Cortex"}
            >
                {isPlaying ? <Volume2 size={20} /> : <VolumeX size={20} />}
            </button>
            {isPlaying && currentTrack && (
                <span className="text-xs text-zinc-500 animate-pulse truncate max-w-[150px]">
                    {currentTrack}
                </span>
            )}
        </div>
    );
}
