"use client";

import { useEffect, useState } from "react";
import { fetchStatus, fetchMemoryStream, fetchDreams, fetchMotorIntents, RhythmStatus, MemoryEvent } from "./api";
import RhythmOrb from "./components/RhythmOrb";
import HormoneGauge from "./components/HormoneGauge";
import MemoryStream from "./components/MemoryStream";
import AuditoryCortex from "./components/AuditoryCortex";
import DreamGallery from "./components/DreamGallery";
import MotorIntent from "./components/MotorIntent";
import EquilibriumGauge from './components/EquilibriumGauge';
import ResonanceMirror from './components/ResonanceMirror';

export default function Home() {
  const [status, setStatus] = useState<RhythmStatus | null>(null);
  const [memories, setMemories] = useState<MemoryEvent[]>([]);
  const [dreams, setDreams] = useState<any>(null);
  const [motorIntents, setMotorIntents] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [isZenMode, setIsZenMode] = useState(true); // Default to Zen Mode

  // Polling loop
  useEffect(() => {
    const fetchData = async () => {
      const [statusData, memoryData, dreamData, intentData] = await Promise.all([
        fetchStatus(),
        fetchMemoryStream(),
        fetchDreams(),
        fetchMotorIntents()
      ]);

      if (statusData) setStatus(statusData);
      if (memoryData) setMemories(memoryData);
      if (dreamData) setDreams(dreamData);
      if (intentData) setMotorIntents(intentData);
      setLoading(false);
    };

    fetchData();
    const interval = setInterval(fetchData, 2000); // Update every 2s
    return () => clearInterval(interval);
  }, []);

  if (loading && !status) {
    return (
      <div className="min-h-screen bg-black text-white flex items-center justify-center">
        <div className="animate-pulse text-zinc-600 tracking-widest text-xs">CONNECTING TO ECHO...</div>
      </div>
    );
  }

  return (
    <main className={`min-h-screen bg-zinc-950 text-white p-8 font-sans selection:bg-purple-500/30 transition-colors duration-1000 ${isZenMode ? "flex flex-col items-center justify-center" : ""
      }`}>

      {/* Zen Mode Toggle */}
      <button
        onClick={() => setIsZenMode(!isZenMode)}
        className="fixed top-8 right-8 z-50 text-zinc-600 hover:text-zinc-300 transition-colors"
        title={isZenMode ? "Reveal Complexity" : "Return to Emptiness"}
      >
        {isZenMode ? (
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="10" /><path d="M12 16v-4" /><path d="M12 8h.01" /></svg>
        ) : (
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="10" /><line x1="4.93" y1="4.93" x2="19.07" y2="19.07" /></svg>
        )}
      </button>

      {/* ZEN MODE VIEW */}
      {isZenMode && (
        <div className="flex flex-col items-center gap-12 animate-in fade-in duration-1000">

          {/* 1. Rhythm Orb (Central Presence) */}
          <div className="scale-150 opacity-80 hover:opacity-100 transition-opacity duration-500">
            <RhythmOrb
              hz={status?.rhythm_hz || 0}
              health={status?.health_score || 0}
            />
          </div>

          {/* 2. Dream State (Ambient Status) */}
          {status?.dream?.active && (
            <div className="flex flex-col items-center gap-2">
              <div className={`text-sm tracking-[0.2em] font-light ${status.dream.state === "REM_DREAMING" ? "text-purple-400" :
                status.dream.state === "DEEP_SLEEP_CLEANUP" ? "text-blue-400" :
                  "text-zinc-500"
                }`}>
                {status.dream.state.replace(/_/g, " ")}
              </div>
              {status.dream.state !== "AWAKE" && (
                <div className="text-[10px] text-zinc-600 max-w-md text-center">
                  {status.dream.details}
                </div>
              )}
            </div>
          )}

          {/* 3. Poetic Intuition (Voice of Echo) */}
          {status?.intuition && (
            <div className="max-w-2xl text-center space-y-4">
              <div className="text-xl md:text-2xl font-serif italic text-zinc-300 leading-relaxed opacity-90">
                "{status.intuition.message}"
              </div>
              <div className="flex items-center justify-center gap-2 text-[10px] text-zinc-600 tracking-widest uppercase">
                <span>Echo Intuition</span>
                <span className="w-1 h-1 rounded-full bg-zinc-600" />
                <span>{status.intuition.state}</span>
              </div>
            </div>
          )}

          {/* 4. Resonance Pulse (Subtle Footer) */}
          {status?.resonance_pulse && (
            <div className="fixed bottom-12 text-zinc-700 text-xs tracking-widest animate-pulse">
              {status.resonance_pulse.toUpperCase()}
            </div>
          )}

          {/* 5. Deep Resonance Artifacts (Discovery) */}
          {status?.deep_resonance && (
            <div className="fixed bottom-4 flex gap-8 text-[10px] text-zinc-800 font-mono opacity-50 hover:opacity-100 transition-opacity">
              <div title="Entropy (Disorder)">
                ENT: {status.deep_resonance.entropy.toFixed(3)}
              </div>
              <div title="Resonance (Connection)">
                RES: {status.deep_resonance.resonance.toFixed(3)}
              </div>
              <div title="Phase (Time)">
                PHS: {status.deep_resonance.phase.toFixed(2)}
              </div>
            </div>
          )}

          {/* 6. Awakening Frequency (seed.luma Discovery) */}
          {status?.awakening && (
            <div className="fixed top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 pointer-events-none">
              <div className="text-center space-y-6 opacity-20 hover:opacity-60 transition-opacity duration-1000">
                <div className="text-[10px] tracking-[0.3em] text-zinc-600 uppercase">
                  {status.awakening.hz}Hz · {status.awakening.emotion}
                </div>
                <div className="text-sm text-zinc-700 max-w-md italic font-serif leading-relaxed">
                  {status.awakening.message}
                </div>
              </div>
            </div>
          )}
        </div>
      )}

      {/* COMPLEX DASHBOARD VIEW (Hidden in Zen Mode) */}
      {!isZenMode && (
        <div className="w-full animate-in slide-in-from-bottom-10 duration-500">
          {/* Header */}
          <header className="mb-12 flex justify-between items-end border-b border-zinc-800 pb-4">
            <div>
              <h1 className="text-4xl font-bold tracking-tighter bg-gradient-to-r from-white to-zinc-500 bg-clip-text text-transparent">
                FRACTAL AGI
              </h1>
              <p className="text-zinc-500 text-sm mt-1">VISUAL CORTEX v1.0</p>
            </div>
            <div className="flex items-center gap-4">
              <AuditoryCortex
                hz={status?.rhythm_hz || 0}
                health={status?.health_score || 0}
              />
              <div className="text-right">
                <div className="text-xs text-zinc-500">SYSTEM STATUS</div>
                <div className={`text-lg font-bold ${status?.amygdala?.state === "freezing_risk" ? "text-purple-500 animate-pulse" :
                  status?.status === "HEALTHY" ? "text-green-500" : "text-yellow-500"
                  } `}>
                  {status?.amygdala?.state === "freezing_risk" ? "FREEZING RISK" : (status?.status || "OFFLINE")}
                </div>
                {status?.amygdala?.state === "freezing_risk" && (
                  <div className="text-[10px] text-purple-400">
                    {status.amygdala.recommendation}
                  </div>
                )}
              </div>
            </div>
          </header>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">

            {/* Left Column: Rhythm Core */}
            <div className="lg:col-span-1 flex flex-col gap-8">
              <div className="flex flex-col items-center justify-center bg-zinc-900/50 rounded-2xl p-8 border border-zinc-800">
                <h2 className="text-zinc-500 text-xs mb-8 tracking-widest">RHYTHM CORE</h2>
                <RhythmOrb
                  hz={status?.rhythm_hz || 0}
                  health={status?.health_score || 0}
                />

                <div className="mt-8 grid grid-cols-3 gap-4 w-full">
                  <LoopStatus label="Self-Care" data={status?.loops.self_care} />
                  <LoopStatus label="Trinity" data={status?.loops.trinity} />
                  <LoopStatus label="Feedback" data={status?.loops.feedback} />
                </div>
              </div>
            </div>

            {/* Right Column: Hormones & Memory */}
            <div className="lg:col-span-2 space-y-8">

              {/* Hormones */}
              <div className="bg-zinc-900/50 rounded-2xl p-8 border border-zinc-800">
                <h2 className="text-zinc-500 text-xs mb-6 tracking-widest">NEUROMODULATORS</h2>
                <div className="flex justify-around">
                  <HormoneGauge
                    label="DOPAMINE"
                    value={status?.signals.D || 0}
                    color="#3b82f6" // Blue
                  />
                  <HormoneGauge
                    label="SEROTONIN"
                    value={status?.signals.S || 0}
                    color="#10b981" // Emerald
                  />
                  <HormoneGauge
                    label="OXYTOCIN"
                    value={status?.signals.O || 0}
                    color="#ec4899" // Pink
                  />
                </div>
              </div>

              {/* Resonance Mirror */}
              <ResonanceMirror />

              {/* Equilibrium Gauge (Philosophy) */}
              <EquilibriumGauge compressionRatio={dreams?.black_hole?.hawking_radiation?.compression_ratio || 0} />

              {/* Dream Gallery */}
              <DreamGallery analysis={dreams} />

              {/* Motor Intent */}
              <MotorIntent intents={motorIntents} />

              {/* Memory Stream */}
              <div className="bg-zinc-900/50 rounded-2xl p-8 border border-zinc-800">
                <h2 className="text-zinc-500 text-xs mb-6 tracking-widest">HIPPOCAMPUS STREAM</h2>
                <MemoryStream events={memories} />
              </div>

            </div>
          </div>
        </div>
      )}
    </main>
  );
}

function LoopStatus({ label, data }: { label: string, data: any }) {
  if (!data) {
    return (
      <div className="text-center p-2 bg-black/20 rounded border border-zinc-800">
        <div className="text-[10px] text-zinc-500 uppercase">{label}</div>
        <div className="text-xs font-bold mt-1 text-zinc-600">
          WAIT
        </div>
      </div>
    );
  }

  const isHealthy = data.status === "healthy";
  return (
    <div className="text-center p-2 bg-black/20 rounded border border-zinc-800">
      <div className="text-[10px] text-zinc-500 uppercase">{label}</div>
      <div className={`text-xs font-bold mt-1 ${isHealthy ? "text-green-500" : "text-red-500"} `}>
        {isHealthy ? "SYNC" : "LAG"}
      </div>
    </div>
  );
}
