import React from 'react';

interface EquilibriumGaugeProps {
    compressionRatio: number;
}

const EquilibriumGauge: React.FC<EquilibriumGaugeProps> = ({ compressionRatio }) => {
    // Log scale mapping for visualization
    // Range: 0.1 (Fear) ~ 1000 (Bias)
    // Equilibrium: 2 ~ 100

    const getPosition = (ratio: number) => {
        if (!ratio || ratio <= 0) return 0;
        // Log10 scale: 0.1 -> -1, 1 -> 0, 10 -> 1, 100 -> 2, 1000 -> 3
        const logVal = Math.log10(ratio);
        // Map -1 ~ 3 to 0% ~ 100%
        // Min: -1 (0.1), Max: 3 (1000) -> Range: 4
        const percent = ((logVal + 1) / 4) * 100;
        return Math.min(Math.max(percent, 0), 100);
    };

    const position = getPosition(compressionRatio);

    // Status Text & Color
    let statusText = "Unknown";
    let statusColor = "text-gray-500";

    if (compressionRatio < 2.0) {
        statusText = "FEAR (Under-compressed)";
        statusColor = "text-purple-400";
    } else if (compressionRatio > 100.0) {
        statusText = "BIAS (Over-compressed)";
        statusColor = "text-orange-400";
    } else {
        statusText = "DYNAMIC EQUILIBRIUM";
        statusColor = "text-green-400";
    }

    return (
        <div className="bg-gray-900/50 backdrop-blur-sm border border-gray-800 rounded-xl p-4 mb-4">
            <div className="flex justify-between items-center mb-2">
                <h3 className="text-sm font-medium text-gray-400">Philosophy State</h3>
                <span className={`text-xs font-bold ${statusColor} animate-pulse`}>
                    {statusText}
                </span>
            </div>

            <div className="relative h-6 bg-gray-800 rounded-full overflow-hidden shadow-inner">
                {/* Zones Background */}
                <div className="absolute inset-0 flex opacity-30">
                    <div className="w-[25%] bg-purple-600" title="Fear Zone (< 2.0)"></div>
                    <div className="w-[50%] bg-green-600" title="Equilibrium Zone (2.0 ~ 100.0)"></div>
                    <div className="w-[25%] bg-orange-600" title="Bias Zone (> 100.0)"></div>
                </div>

                {/* Marker */}
                <div
                    className="absolute top-0 bottom-0 w-1 bg-white shadow-[0_0_10px_rgba(255,255,255,0.8)] transition-all duration-1000 ease-out"
                    style={{ left: `${position}%` }}
                >
                    <div className="absolute -top-1 -left-1.5 w-4 h-8 bg-white/20 rounded-full blur-sm"></div>
                </div>
            </div>

            <div className="flex justify-between text-[10px] text-gray-500 mt-1 font-mono">
                <span>0.1x (Chaos)</span>
                <span className="text-center">Current: {compressionRatio?.toFixed(1)}x</span>
                <span>1000x (Dogma)</span>
            </div>
        </div>
    );
};

export default EquilibriumGauge;
