"use client";

import { motion } from "framer-motion";
import { Zap, AlertTriangle, Shield, Sparkles, Moon } from "lucide-react";

interface MotorIntentProps {
    intents: any[];
}

export default function MotorIntent({ intents }: MotorIntentProps) {
    if (!intents || intents.length === 0) {
        return (
            <div className="w-full h-32 bg-zinc-900/50 rounded-2xl p-6 border border-zinc-800 flex items-center justify-center text-zinc-600">
                No motor reflexes triggered yet...
            </div>
        );
    }

    return (
        <div className="w-full bg-zinc-900/50 rounded-2xl p-6 border border-zinc-800">
            <h2 className="text-zinc-500 text-xs tracking-widest mb-4 flex items-center gap-2">
                <Zap size={14} />
                MOTOR CORTEX (REFLEXES)
            </h2>

            <div className="space-y-2">
                {intents.slice(0, 5).map((intent, idx) => (
                    <IntentCard key={idx} intent={intent} />
                ))}
            </div>
        </div>
    );
}

function IntentCard({ intent }: { intent: any }) {
    const icon = getIconForType(intent.type);
    const color = getColorForPriority(intent.priority);

    return (
        <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            className={`p-3 rounded-lg border ${color.border} ${color.bg} flex items-start gap-3`}
        >
            <div className={`${color.icon} mt-0.5`}>
                {icon}
            </div>
            <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2 mb-1">
                    <span className={`text-xs font-bold ${color.text}`}>
                        {intent.type.replace(/_/g, " ")}
                    </span>
                    <span className="text-[10px] text-zinc-600 font-mono">
                        {new Date(intent.timestamp).toLocaleTimeString()}
                    </span>
                </div>
                <p className="text-xs text-zinc-400 leading-relaxed">
                    {intent.reason}
                </p>
            </div>
        </motion.div>
    );
}

function getIconForType(type: string) {
    switch (type) {
        case "FORCED_SLEEP":
            return <Moon size={16} />;
        case "PATTERN_BREAK":
            return <AlertTriangle size={16} />;
        case "SAFETY_CHECK":
            return <Shield size={16} />;
        case "CREATIVE_SPARK":
            return <Sparkles size={16} />;
        default:
            return <Zap size={16} />;
    }
}

function getColorForPriority(priority: string) {
    switch (priority) {
        case "CRITICAL":
            return {
                bg: "bg-red-500/10",
                border: "border-red-500/30",
                text: "text-red-400",
                icon: "text-red-500"
            };
        case "HIGH":
            return {
                bg: "bg-yellow-500/10",
                border: "border-yellow-500/30",
                text: "text-yellow-400",
                icon: "text-yellow-500"
            };
        case "LOW":
            return {
                bg: "bg-blue-500/10",
                border: "border-blue-500/30",
                text: "text-blue-400",
                icon: "text-blue-500"
            };
        default:
            return {
                bg: "bg-zinc-800/50",
                border: "border-zinc-700",
                text: "text-zinc-300",
                icon: "text-zinc-500"
            };
    }
}
