"use client";

import { motion } from "framer-motion";

interface HormoneGaugeProps {
    label: string;
    value: number; // 0.0 to 1.0
    color: string;
}

export default function HormoneGauge({ label, value, color }: HormoneGaugeProps) {
    const percentage = Math.min(Math.max(value * 100, 0), 100);

    return (
        <div className="flex flex-col items-center gap-2 w-24">
            <div className="relative w-4 h-32 bg-gray-800 rounded-full overflow-hidden border border-gray-700">
                <motion.div
                    className="absolute bottom-0 w-full rounded-b-full"
                    style={{ backgroundColor: color }}
                    initial={{ height: 0 }}
                    animate={{ height: `${percentage}%` }}
                    transition={{ type: "spring", stiffness: 50 }}
                />
            </div>
            <div className="text-center">
                <div className="text-xs font-bold text-gray-300">{label}</div>
                <div className="text-[10px] font-mono text-gray-500">{value.toFixed(2)}</div>
            </div>
        </div>
    );
}
